<template>
  <div
    class="relative flex flex-col min-w-0 break-words w-full mb-6 shadow-lg rounded bg-blueGray-700"
  >
    <div class="rounded-t mb-0 px-4 py-3 bg-transparent">
      <div class="flex flex-wrap items-center">
        <div class="relative w-full max-w-full flex-grow flex-1">
          <h6 class="uppercase text-blueGray-100 mb-1 text-xs font-semibold">
            
          </h6>
          <h2 class="text-white text-xl font-semibold">
            
          </h2>
        </div>
      </div>
    </div>
    <div class="p-4 flex-auto">
      <!-- Chart -->
      <div class="relative h-350-px">
        <canvas id="line-chart"></canvas>
      </div>
    </div>
  </div>
</template>
<script>
import axios from 'axios'
import Chart from "chart.js";

export default {
  mounted: function () {
    axios
    .post(`${window.location.origin}/userData`)
    .then(response => {
      const BingData = response.data.filter(item => {
        return item.model === "Bing";
      })
      const ChatGPTData = response.data.filter(item => {
        return item.model === "ChatGPT";
      })
      const getCountData = (data) => {
        let today = new Date()
        let filteredData = data.filter(item => {
          // 
          let diff = today - new Date(item.time);
          // 
          let days = diff / (1000 * 60 * 60 * 24)
          // 7
          return days <= 7
        })
        let groupedData = filteredData.reduce((acc, cur) => {
          // yyyy-mm-dd
          let dateStr = new Date(cur.time).getDay() - 1
          // 0
          if (!acc[dateStr]) {
            acc[dateStr] = 0
          }
          // 
          acc[dateStr] += 1
          // 
          return acc
        }, {})
        return Array.from({length: 7}, (v, i) => groupedData[i] || 0)
      }

      this.$nextTick(function () {
        var config = {
          type: "line",
          data: {
            labels: [
              "",
              "",
              "",
              "",
              "",
              "",
              "",
            ],
            datasets: [
              {
                label: "Bing",
                backgroundColor: "#4c51bf",
                borderColor: "#4c51bf",
                data: getCountData(BingData),
                fill: false,
              },
              {
                label: "ChatGPT",
                fill: false,
                backgroundColor: "#fff",
                borderColor: "#fff",
                data: getCountData(ChatGPTData),
              }
            ],
          },
          options: {
            maintainAspectRatio: false,
            responsive: true,
            title: {
              display: false,
              text: "Sales Charts",
              fontColor: "white",
            },
            legend: {
              labels: {
                fontColor: "white",
              },
              align: "end",
              position: "bottom",
            },
            tooltips: {
              mode: "index",
              intersect: false,
            },
            hover: {
              mode: "nearest",
              intersect: true,
            },
            scales: {
              xAxes: [
                {
                  ticks: {
                    fontColor: "rgba(255,255,255,.7)",
                  },
                  display: true,
                  scaleLabel: {
                    display: false,
                    labelString: "Month",
                    fontColor: "white",
                  },
                  gridLines: {
                    display: false,
                    borderDash: [2],
                    borderDashOffset: [2],
                    color: "rgba(33, 37, 41, 0.3)",
                    zeroLineColor: "rgba(0, 0, 0, 0)",
                    zeroLineBorderDash: [2],
                    zeroLineBorderDashOffset: [2],
                  },
                },
              ],
              yAxes: [
                {
                  ticks: {
                    fontColor: "rgba(255,255,255,.7)",
                  },
                  display: true,
                  scaleLabel: {
                    display: false,
                    labelString: "Value",
                    fontColor: "white",
                  },
                  gridLines: {
                    borderDash: [3],
                    borderDashOffset: [3],
                    drawBorder: false,
                    color: "rgba(255, 255, 255, 0.15)",
                    zeroLineColor: "rgba(33, 37, 41, 0)",
                    zeroLineBorderDash: [2],
                    zeroLineBorderDashOffset: [2],
                  },
                },
              ],
            },
          },
        };
        var ctx = document.getElementById("line-chart").getContext("2d");
        window.myLine = new Chart(ctx, config);
      });
    })
    .catch((error) => {
      console.log(error);
    })
  },
};
</script>
